import React, { Fragment } from 'react';
import Select from 'react-select';
import StudentsTable from './Components/StudentsTable.jsx';
import fb from './firebase.service';
import * as data from './data.json';

const { mentorsStudents, students, tasks } = data.default;

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedOption: JSON.parse(window.localStorage.getItem('selectedItem')),
      user: 'null',
    };

    this.componentDidMount = () => {
      const prevLoggedUser = JSON.parse(window.localStorage.getItem('loggedUser'));
      if (prevLoggedUser) {
        const isMentor = this.checkIsMentor(prevLoggedUser);
        if (isMentor) {
          this.handleChange({ key: isMentor.id, value: isMentor.mentor, label: isMentor.mentor });
        }
      }
    };

    fb.auth.onAuthStateChanged(
      user => this.setState(
        { user: user ? { displayName: user.displayName, photoURL: user.photoURL } : null },
      ),
    );
  }

  login = () => {
    fb.login().then(({ user, additionalUserInfo }) => {
      const userGitHub = additionalUserInfo.profile.html_url;
      this.setState({ user });
      const isMentor = this.checkIsMentor(userGitHub);
      if (isMentor) {
        window.localStorage.setItem('loggedUser', JSON.stringify(userGitHub));
        this.handleChange({ key: isMentor.id, value: isMentor.mentor, label: isMentor.mentor });
      }
    }).catch((error) => {
      throw error.message;
    });
  }

  logout = () => {
    fb.logout().then(() => {
      this.setState({ user: null });
      window.localStorage.setItem('loggedUser', JSON.stringify(null));
    }).catch((error) => {
      throw error.message;
    });
  }

  handleChange = (selectedOption) => {
    this.setState({ selectedOption });
    window.localStorage.setItem('selectedItem', JSON.stringify(selectedOption));
  }

  checkIsMentor = (github) => {
    const result = mentorsStudents.filter(user => user.mentorGithub === github);
    return result[0];
  }

  render() {
    const { user, selectedOption } = this.state;
    let resultTable;

    if (selectedOption != null) {
      resultTable = (<StudentsTable tasks={tasks} score={students} studentsOfMentor={mentorsStudents[selectedOption.key].students} />);
    } else {
      resultTable = <div />;
    }
    return (
      <Fragment>
        <div className="main-wrapper">
          <header>
            <section className="login-wrapper">
              {user && <img src={user.photoURL} alt="avatar" />}
              {user && <p className="display-name">{user.displayName}</p>}
              {user
                ? <button type="button" onClick={this.logout}>Logout</button>
                : <button type="button" onClick={this.login}>Login</button>
              }
            </section>
          </header>
          <main>
            <Select
              className="select-input"
              value={selectedOption}
              placeholder="Select mentor..."
              options={mentorsStudents.map(mentor => (
                { key: mentor.id, value: mentor.mentor, label: mentor.mentor }))}
              onChange={this.handleChange}
            />
            {resultTable}
          </main>
        </div>
      </Fragment>
    );
  }
}

export default App;
