import React from 'react';
import PropTypes from 'prop-types';

const uuidv1 = require('uuid/v1');

class StudentsTable extends React.Component {
  selectColor = (status, existMark) => {
    let resultColor = '#d3c7c48f';
    if (existMark) {
      resultColor = '#44b84e';
    } else {
      if (status === 'Checked') {
        resultColor = '#b84844';
      }
      if (status === 'Checking') {
        resultColor = 'df9988';
      }
      if (status === 'In Progress') {
        resultColor = '#e9cc28';
      }
      if (status === 'ToDo') {
        resultColor = '#d3c7c48f';
      }
    }
    return resultColor;
  }

  createRow(task, status) {
    const { studentsOfMentor, score } = this.props;
    return studentsOfMentor.map(student => (
      <td key={uuidv1()} bgcolor={this.selectColor(status, score[student].includes(task.trim()))} />
    ));
  }

  render() {
    const { tasks, studentsOfMentor } = this.props;
    return (
      <div>
        <table>
          <tbody>
            <tr>
              <th>Task\Student</th>
              {studentsOfMentor.map(record => (<th key={uuidv1()}>{record}</th>))}
            </tr>
            {tasks.map(task => (
              (
                <tr key={uuidv1()}>
                  <td>
                    {task.task}
                  </td>
                  {this.createRow(task.task, task.status)}
                </tr>
              )
            ))
            }
          </tbody>
        </table>
      </div>

    );
  }
}

StudentsTable.propTypes = {
  studentsOfMentor: PropTypes.arrayOf(PropTypes.string).isRequired,
  score: PropTypes.shape({}).isRequired,
  tasks: PropTypes.arrayOf(PropTypes.object).isRequired,
};


export default StudentsTable;
